.checkbox {
    appearance: none;
    width: 15px;
    height: 15px;
    background: #dcdbf8;
    cursor: pointer;
}
.checkbox:checked {
    appearance: none;
    background-image: url("/src/images/checkbox.png");
}
.spanClassName {
    margin-left: 15px;
}

.label {
    margin-top: 15px;
    max-width: 200px;
    width: 100%;
    cursor: pointer;
    display: flex;
}
